.wrapper {
    width: 90%;
    max-width: 1350px;
    margin: 0 auto;
}
.head {
    padding: 50px 0 50px 0;
}
.head h1 {
    font-size: 36px;
    margin-bottom: 18px;
}
.head p {
    color: #e6e6e6;
    font-size: 24px;
}
.container ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.container ul li {
    cursor: pointer;
    width: 23%;
    border-radius: 10px;
    margin: 0 20px 20px 0;
    transition: all .3s ease;
}
.container ul li:hover {
    transform: scale(1.03);
    box-shadow: #046df67d 0px 4px 10px 0px;}
.container ul li .preview img {
    width: 100%;
    display: block;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}
.container ul li .info {
    background: #f0f0f0;
    padding:12px  20px ;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}
.container ul li .info h2 {
    font-size: 20px;
    margin-bottom: 10px;
}
.container ul li .info .location{
    display: flex;
    align-items: center;
}
.container ul li .info .location small{
    margin-right: 4px;
    width: 10px;
}
.container ul li .info .location small img {
    width: 100%;
    display: block;
}
.container ul li .info .location span {
    font-size: 16px;
}
/* SingleView.js */
.content-box {
    width: 70%;
    margin: 0 auto;
    padding-top: 80px;
}
.content-box .top-box h2 {
    font-size: 36px;

}
.content-box .bottom-box{
    display: flex;
    align-items: center;
    margin-top: 14px;
}

.content-box .bottom-box small{
    padding: 8px 16px;
    border: 1px solid #bdbdbd;
    color: #bdbdbd;
    border-radius: 40px;
    display: inline-block;
    margin-right: 20px;
}
.content-box .bottom-box .location{
    display: flex;
    align-items: center;
}
.content-box .bottom-box .location span{
    font-size: 18px;
    margin-left: 6px;
    font-weight: 600;
    color: #bdbdbd;
}
.content-box  .preview {
    width: 500px;
    margin: 30px 0;
}
.content-box .image-box  {
    display: flex;
    align-items: center;
}
.content-box .image-box .preview img {
    width: 100%;
    display: block;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}
.content-box  .image-box .grid{
    width: 60%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}
.content-box  .image-box .grid span {
    width: 220px;
    display: block;
    margin: 0 10px 10px 20px;
}
.content-box  .image-box .grid span img {
    width: 100%;
    display: block;
}
.content-box  .image-box .grid span:nth-child(2n) img{
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}
.content-box .text-box h2 {
    font-size: 28px;
    margin-bottom: 20px;
}
.content-box .text-box p {
    line-height: 1.5em;
    font-size: 16px;
}