header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100px;
    padding: 0 100px;
}
header .logo {
    width: 140px;
}
header .logo img {
    width: 100%;
    display: block;
}
header .login a {
    outline: none;
    border: none;
    padding: 14px 50px;
    font-size: 20px;
    border-radius: 6px;
    background: #046bf6;
    color: #fff;
    font-weight:600;
    cursor: pointer;
    transition: .3s ease;
}
header .login a:hover {
    background: #00295f;
}